import './AddColorForm.scss'

export const AddColorForm = ({ formIsOpen, onAddColor }) => {
  let _title, _color
  const formClass = formIsOpen ? 'add-color-form active' : 'add-color-form'

  const onSubmit = () => {
    const title = _title.val()
    const color = _color.val()
    if (color && title) {
      return onAddColor(title, color)
    }
    return
  }

  return (
    <div className={ formClass }>
      <p>Add your new color</p>
      <p>
        <span>Title: </span>
        <input ref={(input) => _title = input} />
      </p>
      <p>
        <span>Color: </span>
        <input ref={(input) => _color = input} />
      </p>
      <button onClick={onSubmit}>Submit</button>
    </div>
  )
}



